#footer {
  display: -ms-flexbox;
  display: flex;
  position: relative;
  top: -2em;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 96%;
  margin-top: 1%;
}
#footer>div {
  padding: .5em;
  width: 32.5%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 10em;
  border: 1px solid #757575;
  border-radius: .2em;
  overflow: hidden;
  background: #f0f0f0;
}
.weather {
  background: #fff !important;
}
.weather h4 {
  text-align: center;
  margin: auto;
  padding-top: 1em;
}
.spinner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.spinner img {
  height: 10em;
}
.city {
  height: 1.2em;
  display: block;
  border: 1px solid;
  border-radius: .2em;
  margin-bottom: .2em;
  padding: .1em;
  background: #fff;
}
.favorite {
  overflow-y: scroll !important;
}
.favorite .city {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.delete {
  margin: .1em;
  border-radius: 50%;
  border: none;
  width: 1.2em;
  height: 1.2em;
  background: url(data:image/svg+xml;utf8;base64,) no-repeat;
  background-size: 100%;
  background-position: center;
}


